import styled from "styled-components";
import React, { useState } from "react";

const Wrapper = styled.section`
  font-size: 24px;
  > ul {
    display: flex;

    > li {
      width: 50%;
      text-align: center;
      padding: 16px 0;
      position: relative;
      &.selected::after {
        content: "";
        display: block;
        height: 3px;
        background: #2f3542;
        position: absolute;
        bottom: 0;
        width: 100%;
        left: 0;
      }
    }
  }
`;

type Props = {
  value: "-" | "+";
  onChange: (value: "-" | "+") => void;
};

export const CategorySection: React.FC<Props> = (props) => {
  const categoryMap = { "-": "支出", "+": "收入" };
  type X = typeof categoryMap;
  type Keys = keyof X;

  const [categoryList] = useState<Keys[]>(["-", "+"]);

  const category = props.value;
  const setCategory = props.onChange;

  return (
    <Wrapper>
      <ul>
        {categoryList.map((c) => (
          <li
            className={category === c ? "selected" : undefined}
            onClick={() => {
              setCategory(c);
            }}
            key={c}
          >
            {categoryMap[c]}
          </li>
        ))}
      </ul>
    </Wrapper>
  );
};
